.blog_grid {
  display: grid;
  grid-gap: 2em;
  grid-template-columns: repeat(auto-fill, minmax(250px,1fr));
  grid-auto-rows: 40px;
  padding: 2em;
  background: url('/images/photos/blogBG2.jpg') center center fixed;
  background-size: cover;
  perspective: 1000px;
  &--item {
    border-radius: 5px;
    background-color: #F6F6F6;
    box-shadow: 0 0 10px rgba(0,0,0,0.2);
    transition:
      transform 0.25s linear,
      box-shadow 0.25s linear;
    &:hover {
      transform: translateZ(10px);
      box-shadow: 5px 5px 10px rgba(0,0,0,0.2);
    }
    .content {
      display: flex;
      flex-direction: column;
      align-items: center;
      &--header {
        font-weight: 700;
        font-size: 1.3em;
        text-decoration: none;
        color: #000;
      }
      &--image {
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        width: 100%;
      }
      &--desc {
        padding: 1em;
      }
    }
  }
  .last_link {
    display: flex;
    justify-content: center;
    align-items: center;
    .content a {
      color: #000;
      font-size: 1.2em;
    }
  }
}


